/\n\theight: 0; /* 1 */\n\toverflow: visible; /* 2 */\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n\npre {\n\tfont-family: monospace, monospace; /* 1 */\n\tfont-size: 1em; /* 2 */\n}\n\n/* Text-level semantics\n   ========================================================================== */\n\n/**\n * Remove the gray background on active links in IE 10.\n */\n\na {\n\tcolor: white;\n\ttext-decoration: none;\n}\n\n/**\n * 1. Remove the bottom border in Chrome 57-\n * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.\n */\n\nabbr[title] {\n\tborder-bottom: none; /* 1 */\n\ttext-decoration: underline; /* 2 */\n\ttext-decoration: underline dotted; /* 2 */\n}\n\n/**\n * Add the correct font weight in Chrome, Edge, and Safari.\n */\n\nb,\nstrong {\n\tfont-weight: bolder;\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n\ncode,\nkbd,\nsamp {\n\tfont-family: monospace, monospace; /* 1 */\n\tfont-size: 1em; /* 2 */\n}\n\n/**\n * Add the correct font size in all browsers.\n */\n\nsmall {\n\tfont-size: 80%;\n}\n\n/**\n * Prevent `sub` and `sup` elements from affecting the line height in\n * all browsers.\n */\n\nsub,\nsup {\n\tfont-size: 75%;\n\tline-height: 0;\n\tposition: relative;\n\tvertical-align: baseline;\n}\n\nsub {\n\tbottom: -0.25em;\n}\n\nsup {\n\ttop: -0.5em;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Remove the border on images inside links in IE 10.\n */\n\nimg {\n\tborder-style: none;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * 1. Change the font styles in all browsers.\n * 2. Remove the margin in Firefox and Safari.\n */\n\nbutton,\nfigure,\ninput,\noptgroup,\nselect,\ntextarea {\n\tfont-family: inherit; /* 1 */\n\tfont-size: 100%; /* 1 */\n\tline-height: 1.15; /* 1 */\n\tmargin: 0; /* 2 */\n\toutline: none;\n}\n\n//list\nul {\n\tlist-style: none;\n\tmargin: 0;\n\tpadding: 0;\n}\n\n/**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n\nbutton,\ninput {\n\t/* 1 */\n\toverflow: visible;\n}\n\n/**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n\nbutton,\nselect {\n\t/* 1 */\n\ttext-transform: none;\n}\n\n/**\n * Correct the inability to style clickable types in iOS and Safari.\n */\n\nbutton,\n[type=\"button\"],\n[type=\"reset\"],\n[type=\"submit\"] {\n\t-webkit-appearance: button;\n\toutline: none;\n\tborder: none;\n}\n\n/**\n * Remove the inner border and padding in Firefox.\n */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n\tborder-style: none;\n\tpadding: 0;\n}\n\n/**\n * Restore the focus styles unset by the previous rule.\n */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n\toutline: 1px dotted ButtonText;\n}\n\n/**\n * Correct the padding in Firefox.\n */\n\nfieldset {\n\tpadding: 0.35em 0.75em 0.625em;\n}\n\n/**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n */\n\nlegend {\n\tbox-sizing: border-box; /* 1 */\n\tcolor: inherit; /* 2 */\n\tdisplay: table; /* 1 */\n\tmax-width: 100%; /* 1 */\n\tpadding: 0; /* 3 */\n\twhite-space: normal; /* 1 */\n}\n\n/**\n * Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n\tvertical-align: baseline;\n}\n\n/**\n * Remove the default vertical scrollbar in IE 10+.\n */\n\ntextarea {\n\toverflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10.\n * 2. Remove the padding in IE 10.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n\tbox-sizing: border-box; /* 1 */\n\tpadding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n\theight: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n\t-webkit-appearance: textfield; /* 1 */\n\toutline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-decoration {\n\t-webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n\t-webkit-appearance: button; /* 1 */\n\tfont: inherit; /* 2 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in Edge, IE 10+, and Firefox.\n */\n\ndetails {\n\tdisplay: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n\tdisplay: list-item;\n}\n\n/* Misc\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10+.\n */\n\ntemplate {\n\tdisplay: none;\n}\n\n/**\n * Add the correct display in IE 10.\n */\n\n[hidden] {\n\tdisplay: none;\n}\n",".container-layout {\n    display: grid;\n    grid-template-columns: minmax(300px, 459px) 1fr;\n    @include media(lg) {\n        grid-template-columns: 1fr; } }\n\n.flexelement {\n    display: flex;\n    justify-content: space-between;\n    position: relative; }\n\n\n.container {\n    margin: auto;\n    max-width: $max-width;\n    height: 100%;\n    @include media(xl) {\n        max-width: 520px; } }\n\n\n.center-element {\n\tdisplay: flex;\n\tjustify-content: center;\n\tmin-height: 100vh; }\n\n\n\n.flex-start {\n    display: flex;\n    align-items: flex-start;\n    flex-wrap: wrap; }\n\n\n.flexd-two {\n    flex-direction: row; }\n\n.imgres {\n    width: 100%;\n    object-fit: contain; }\n\n\n\n\n@mixin flex-d {\n    &-r {\n        flex-direction: row; }\n\n    &-c {\n        flex-direction: column; } }\n\n\n.flex-d {\n    @include flex-d; }\n\n\n@mixin jc {\n    &-l {\n        justify-content: flex-start !important; }\n\n    &-c {\n        justify-content: center !important; }\n\n    &-r {\n        justify-content: flex-end !important; }\n\n    &-sb {\n        justify-content: space-between !important; }\n\n    &-sa {\n        justify-content: space-around !important; } }\n\n\n\n\n@mixin ai {\n    &-l {\n        align-items: flex-start !important; }\n\n    &-c {\n        align-items: center !important; }\n\n    &-r {\n        align-items: flex-end !important; }\n\n    &-s {\n        align-items: stretch !important; }\n\n    &-b {\n        align-items: baseline !important; } }\n\n\n\n.jc {\n    @include jc; }\n\n.ai {\n    @include ai; }\n\n// gap\n@mixin gap($i) {\n    margin-left: (-$gutter*$i)/2;\n    width: calc(100% + #{$gutter}*#{$i}) !important;\n\n    & > [class^='col-'] {\n        padding: 0px ($gutter*($i))/2; } }\n\n@mixin gapY($i) {\n    & > [class^='col-'] {\n        padding-bottom: $gutter*$i; } }\n\n@for $i from 0 through map-get($col, g) {\n    .g-#{$i} {\n        @include gap($i); }\n    .gy-#{$i} {\n        @include gapY($i); } }\n\n// col layout\n\n// percentage debe estar en la function\n@function columns($columns, $total-columns) {\n    @return percentage($columns / $total-columns); }\n\n\n@for $i from 0 through $maxcol {\n  .col-#{$i} {\n    width: columns($i,$maxcol); } }\n\n.col {\n    width: 100%; }\n// variable editable de 12\n\n.test {\n    height: 200px; }\n\n@each $size in $vp {\n    $key: nth($size, 1);\n    $value: nth($size, 2);\n    @media screen and(min-width: $value) {\n        @for $i from 1 through $maxcol {\n            .col-#{$key}-#{$i} {\n                width: columns($i,$maxcol); } }\n        @for $i from 0 through map-get($col, g) {\n            .g-#{$key}-#{$i} {\n                @include gap($i); }\n            .gy-#{$key}-#{$i} {\n                @include gapY($i); } }\n        .j-#{$key} {\n            @include jc; }\n        .a-#{$key} {\n            @include ai; } } }\n\n\n\n","@mixin media($breakpoint) {\n\t@if $breakpoint == sm {\n\t\t@media only screen and (max-width: 576px) {\n\t\t\t@content;\n\t\t}\n\t} @else if $breakpoint == sm2 {\n\t\t@media only screen and (min-width: 576px) {\n\t\t\t@content;\n\t\t}\n\t} @else if $breakpoint == intermediate {\n\t\t@media only screen and (max-width: 675px) {\n\t\t\t@content;\n\t\t}\n\t} @else if $breakpoint == md {\n\t\t@media only screen and (min-width: 768px) {\n\t\t\t@content;\n\t\t}\n\t} @else if $breakpoint == lg {\n\t\t@media only screen and (max-width: 992px) {\n\t\t\t@content;\n\t\t}\n\t} @else if $breakpoint == xl {\n\t\t@media only screen and (max-width: 1200px) {\n\t\t\t@content;\n\t\t}\n\t} @else if $breakpoint == xxl {\n\t\t@media only screen and (min-width: 1024px) and (max-width: 1224px) {\n\t\t\t@content;\n\t\t}\n\t}\n}\n","// ancho maximo template\n$gutter: 0.96rem;\n$max-width: 710px;\n$font: unquote(\"'Raleway', sans-serif\");\n$default-color: #050023;\n\n$no-line-height: 1;\n$border-radius: 30px;\n\n$rem: 0.5rem;\n$remtext: 0.5rem;\n\n// maximo de col de grilla\n$maxcol: 12;\n// viewport brakpoint\n$vp: (\n\t\"sm\": 540px,\n\t\"md\": 720px,\n\t\"lg\": 960px,\n\t\"xl\": 1140px,\n);\n$col: (\n\t\"n\": 16,\n\t\"g\": 6,\n);\n//margenes\n\n// colores\n$colorMap: (\n\twhiteweather: #fff,\n\tdarkBlue: #100e1d,\n\tMidnightBlue: #1e213a,\n\tdark: #110e3c,\n\tdark2: #110e3c,\n\tgreylight: #e7e7eb,\n\tgreydark: #a09fb1,\n\tgreyback: #6e707a,\n\tviolet: #3c47e9,\n\tyellow: #ffec65,\n\tfahrenheit: #585676,\n\tfahrenheit2: #504e6d,\n\tbordercity: #616475,\n\tdetailscity: #88869d,\n);\n\n$spaceMap: (\n\t0: 0,\n\txxxs: $rem / 2,\n\txxs: $rem,\n\txs: $rem * 1.5,\n\ts: $rem * 2,\n\tm: $rem * 3,\n\tl: $rem * 4,\n\txl: $rem * 6,\n\txxl: $rem * 9,\n\txxxl-a: $rem * 17,\n\txxxl: $rem * 20,\n\txxxxl: $rem * 22.5,\n);\n\n//font-size\n$textSizeMap: (\n\txxs: $remtext + 0.1rem,\n\t//10px\n\txs: $remtext + 0.4rem,\n\t//12px\n\ts: $remtext * 2,\n\t//16px\n\tm: $remtext * 3,\n\t//24px\n\tl: $remtext * 4,\n\t//32\n\txl: $remtext * 6,\n\t//48\n\txxl: $remtext * 16,\n\t//72\n);\n\n//font-weight\n$textWeightsMap: (\n\tregular: 200,\n\tlight: 300,\n\tmedium: 500,\n\tbold: 700,\n\tbolder: 900,\n);\n\n$tokens: (\n\tcolor: $colorMap,\n\ttextSize: $textSizeMap,\n\ttextWeight: $textWeightsMap,\n);\n","@each $color in $colorMap {\n    $key: nth($color, 1);\n    $value: nth($color, 2);\n    .color-#{$key} {\n        color: $value !important; }\n    .back-#{$key} {\n        background-color: $value !important; } }\n\n\n","//margin y padding global desktop\n@each $space in $spaceMap {\n  $key: nth($space, 1);\n  $value: nth($space, 2);\n  .m-#{$key} {\n    margin: $value !important; }\n  .mx-#{$key} {\n    margin-left: $value !important;\n    margin-right: $value !important; }\n  .my-#{$key} {\n    margin-top: $value !important;\n    margin-bottom: $value !important; }\n  .mt-#{$key} {\n    margin-top: $value !important; }\n  .mb-#{$key} {\n    margin-bottom: $value !important; }\n  .ml-#{$key} {\n    margin-left: $value !important; }\n  .mr-#{$key} {\n    margin-right: $value !important; }\n  .p-#{$key} {\n    padding: $value !important; }\n  .px-#{$key} {\n    padding-left: $value !important;\n    padding-right: $value !important; }\n  .py-#{$key} {\n    padding-top: $value !important;\n    padding-bottom: $value !important; }\n  .pt-#{$key} {\n    padding-top: $value !important; }\n  .pb-#{$key} {\n    padding-bottom: $value !important; }\n  .pl-#{$key} {\n    padding-left: $value !important; }\n  .pr-#{$key} {\n    padding-right: $value !important; } }\n\n//margin y padding responsive\n@each $size in $vp {\n    $key: nth($size, 1);\n    $value: nth($size, 2);\n    @media screen and(min-width: $value) {\n        @each $space in $spaceMap {\n            $k: nth($space, 1);\n            $v: nth($space, 2);\n            .m-#{$key}-#{$k} { //first margin\n                margin: $v !important; }\n            .mx-#{$key}-#{$k} {\n                margin-left: $v !important;\n                margin-right: $v !important; }\n            .my-#{$key}-#{$k} {\n                margin-top: $v !important;\n                margin-bottom: $v !important; }\n            .mt-#{$key}-#{$k} {\n                margin-top: $v !important; }\n            .mb-#{$key}-#{$k} {\n                margin-bottom: $v !important; }\n            .ml-#{$key}-#{$k} {\n                margin-left: $v !important; }\n            .mr-#{$key}-#{$k} {\n                margin-right: $v !important; }\n            .p-#{$key}-#{$k} { //second padding\n                padding: $v !important; }\n            .px-#{$key}-#{$k} {\n                padding-left: $v !important;\n                padding-right: $v !important; }\n            .py-#{$key}-#{$k} {\n                padding-top: $v !important;\n                padding-bottom: $v !important; }\n            .pt-#{$key}-#{$k} {\n                padding-top: $v !important; }\n            .pb-#{$key}-#{$k} {\n                padding-top: $v !important; }\n            .pl-#{$key}-#{$k} {\n                padding-left: $v !important; }\n            .pr-#{$key}-#{$k} {\n                padding-right: $v !important; } } } }\n\n.m-auto {\n  margin: auto; }\n\n","body {\n    font-family: $font;\n    overflow-x: hidden;\n    background-color:  map-get($colorMap, back); }\n\n\nh2, h3, h4 {\n\tfont-weight: map-get($textWeightsMap, bold); }\n\n\nh1 {\n    display: inline-block;\n    font-size: map-get($textSizeMap, l);\n    font-weight: map-get($textWeightsMap, bolder);\n    color:  map-get($colorMap, back);\n    ma